const renderCustomizedLabel = (props) => {
    const {
      x, y, width, height, value, data,
    } = props;
    const radius = 10;
  
    return (
      <g>
        <circle cx={x + width / 2} cy={y - radius} r={radius} fill="#8884d8" />
        <text x={x + width / 2} y={y - radius} fill="#fff" textAnchor="middle" dominantBaseline="middle">
          {data.วิชาหลัก + data.กิจกรรมพัฒนาผู้เรียน}
        </text>
      </g>
    );
  };

  const getIntroOfPage = (label) => {
    if (label === 'Page A') {
      return "Page A is about men's clothing";
    } if (label === 'Page B') {
      return "Page B is about women's dress";
    } if (label === 'Page C') {
      return "Page C is about women's bag";
    } if (label === 'Page D') {
      return 'Page D is about household goods';
    } if (label === 'Page E') {
      return 'Page E is about food';
    } if (label === 'Page F') {
      return 'Page F is about baby food';
    }
  };

  const CustomTooltip = ({ active, payload, label }) => {
    if (active) {
      return (
        <div className="custom-tooltip">
          <p className="label">{`${label} : ${payload[0].value}`}</p>
          <p className="intro">{getIntroOfPage(label)}</p>
          <p className="desc">Anything you want can be displayed here.</p>
        </div>
      );
    }
  
    return null;
  };

export default function StackChart({data}) {
  
    const [chartData, setChartData] = React.useState([]);
    const [chartReady, setChartReady] = React.useState(false);
    const classes = useStyles();

    const handleClickDownload = (event) => {
      window.open(event,'_blank');
  }
  
    useEffect(() => {
        // Run! Like go get some data from an API.
        //console.log(orderData);
        //console.log(teachingData);
        //console.log(plcData);
        var teachingData = [];
        if(data.length>0){
            data.sort();
            data.map((obj,index) => {
          var a = "".concat(parseInt(obj.teachingTerm)).concat("/").concat(parseInt(obj.teachingYear));
          var b = ((parseInt(obj.teachingSubjectMajor)*obj.periodTime)/60).toFixed(2);
          var c = ((parseInt(obj.teachingSubjectOthers)*obj.periodTime)/60).toFixed(2);
          teachingData.push({name: a,
                วิชาหลัก: b,
                 กิจกรรมพัฒนาผู้เรียน: c,
                 amt: 2400});
          });
          setChartData(teachingData);
          if(teachingData.length>0){
            setChartReady(true);
          }
        }
        console.log(teachingData);
      }, []);

  return (
    <div>

{ chartReady && data && 
    data.map((obj,index) =>  obj.teachingTable.split(',').length > 0 && ( 
                obj.teachingTable.split(',').map((item, idx) => 
            <IconButton key={idx}  aria-label="share"
             onClick={()=> {handleClickDownload(item)}}>
            <InsertDriveFileIcon/>
            </IconButton> 
        ))
)}
       <Typography gutterBottom variant="subtitle2" component="h6" color="textPrimary" style={{width: "100%"}}>
                    สรุปข้อมูลตารางสอนในแต่ละปีการศึกษา (หน่วยเป็นชั่วโมง)
                  </Typography>
             
        {chartReady  && data && (
              <BarChart
              width={300}
              height={400}
              data={chartData}
              margin={{
                top: 5, right: 5, left: 5, bottom: 5,
              }}
            >
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Legend width={300} wrapperStyle={{ bottom: 0, right: 0, backgroundColor: '#f5f5f5', border: '1px solid #d5d5d5', borderRadius: 3, lineHeight: '40px' }} />
              <Bar dataKey="วิชาหลัก" stackId="a" fill="#8884d8" />
              <Bar dataKey="กิจกรรมพัฒนาผู้เรียน" stackId="a" fill="#82ca9d">

            </Bar>
            </BarChart>
            )}
{chartReady  && data && (
<TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
        <TableRow>
            <TableCell align="center" colSpan={4}>จำนวนชั่วโมงสอน/สัปดหา์</TableCell>
          </TableRow>
          <TableRow>
            <TableCell>ภาคเรียนที่</TableCell>
            <TableCell align="right">วิชาหลัก (ชม.)</TableCell>
            <TableCell align="right">กิจกรรมพัฒนาผู้เรียน (ชม.)</TableCell>
            <TableCell align="right">รวมทั้งสิ้น (ชม.)</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {chartData.map((obj,idx) => (
            <TableRow key={idx}>
              <TableCell component="th" scope="row">
                {obj.name}
              </TableCell>
              <TableCell align="right">{obj.วิชาหลัก}</TableCell>
              <TableCell align="right">{obj.กิจกรรมพัฒนาผู้เรียน}</TableCell>
              <TableCell align="right">{parseFloat(obj.วิชาหลัก)+parseFloat(obj.กิจกรรมพัฒนาผู้เรียน)}</TableCell>
              
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>

)}
    </div>
  );
}