import React from 'react';
import { Theme, createStyles, makeStyles, useTheme, fade} from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import PhotoIcon from '@material-ui/icons/Photo';
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import { Hidden } from '@material-ui/core';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      width: "95%",
      height: "100%",
    },
    details: {
        position: 'relative',
        width: "100%",
        height: "100%",
      display: 'flex',
      flexDirection: 'column',
    },
    content: {
      display: 'block',
      textAlign: "left",
      height: "100%",
      paddingLeft: theme.spacing(2),
      paddingTop: theme.spacing(1),
    },
    coverbook: {
        position: 'relative',
        display: 'flex',
    objectFit: "cover",
        height: "100%",
      right: 0,
    },
    controls: {
      display: 'flex',
      alignItems: 'center',
      paddingLeft: theme.spacing(1),
      paddingBottom: theme.spacing(1),
    },
    playIcon: {
    position: "absolute",
    right: 0,
      height: 38,
      width: 38,
    },
  }),
);

export default function PresentCard({obj}) {
  const classes = useStyles();
  const theme = useTheme();

  const handleClickDownload = (event) => {
    window.open(event,'_blank');
}
  return (
    <div className={classes.root}>
    <Hidden xsDown>
        <div className={classes.coverbook}>
    <LazyLoadImage alt="" effect="blur" width="100%" 
       src={obj.picture.split(',')[0].trim().replace("https://drive.google.com/open?id=", 
       "https://lh3.googleusercontent.com/d/").concat("=w800")} 
       onClick={()=> {handleClickDownload(obj.picture.split(',')[0])}}/>
</div>
    </Hidden>


    <div className={classes.details}>
      <Hidden smUp>
            <LazyLoadImage alt="" effect="blur" width="100%" style={{
    objectFit: "cover",
        height: "100%",}}
       src={obj.picture.split(',')[0].trim().replace("https://drive.google.com/open?id=", 
       "https://lh3.googleusercontent.com/d/").concat("=w800")} 
       onClick={()=> {handleClickDownload(obj.picture.split(',')[0])}}/>

    </Hidden>
        <div className={classes.content}>
          <Typography component="h6" variant="h6">
            {"หัวข้อ ".concat(obj.title)}
          </Typography>
          <Typography variant="subtitle1" color="textSecondary">
            <span>{"วันที่ ".concat(obj.date)}</span>
          </Typography>
          <Typography variant="subtitle1" color="textSecondary">
            <span>{"รายละเอียด: ".concat(obj.description)}</span>
          </Typography>
          <Typography variant="subtitle2" color="textSecondary">
            <span>{"ไฟล์แนบ: "}
            { obj.picture != "" && ( obj.picture.split(',').map((item,ind) => 
            <IconButton key={ind} aria-label="share"
             onClick={()=> {handleClickDownload(item)}}>
            <PhotoIcon style={{color: "#FF8A65"}}/>
            </IconButton> 
        ))}
        { obj.pdf != "" && (obj.pdf.split(',').map((item,ind) => 
            <IconButton key={ind} aria-label="share"
             onClick={()=> {handleClickDownload(item)}}>
            <InsertDriveFileIcon style={{color: "#e57373"}}/>
            </IconButton> 
        ))}
            </span>
          </Typography>
        </div>
      </div>
    </div>
  );
}